.services {
  &__wrapper {
    // margin-top: 32px;
    margin-left: 200px;
    margin-right: 200px;
  }

  &__block {
    margin-top: 92px;
  }

  &__clasify {
    margin-top: 168px;
  }

  &__claim {
    margin-top: 168px;
  }

  &__block,
  &__clasify,
  &__claim {
    max-width: 4000px;
    display: grid;
    gap: 75px;
    grid-template-columns: repeat(2, 1fr);
    margin-bottom: 28px;
  }

  &__heading {
    // margin-top: 80px;
    font-size: 38px;
    font-weight: 800;
    line-height: 51px;
    letter-spacing: 0.4px;
    color: $main-color;
  }

  &__text {
    margin-top: 28px;
    font-size: 16px;
    font-weight: 400;
    line-height: 24px;
    letter-spacing: 0.2px;
    color: $text-color;
  }

  &__includes {
    margin-top: 42px;
    display: flex;
    flex-direction: column;
    justify-content: center;

    &-first {
      display: flex;
      flex-direction: row;
    }

    &-second {
      display: flex;
      flex-direction: row;
    }
  }

  &__extra {
    font-size: 16px;
    font-weight: 400;
    line-height: 24px;
    letter-spacing: 0.2px;
    color: $text-color;
    margin: 0;
  }

  &__button-wrapper {
    margin-top: 32px;
  }

  &__button {
    font-size: 18px;
    padding: 16px 23px;
    background-color: $white-color;
    border-radius: 27px;
    border: 1px solid $main-color;
    color: $main-color;
    transition: background-color 0.3s;
    &:hover {
      // color: $white-color;
      background-color: #eaf3ec;
    }
  }

  &__arrow {
    border: solid $main-color;
    border-width: 0 2px 2px 0;
    display: inline-block;
    padding: 3px;
    transform: rotate(-45deg);
    -webkit-transform: rotate(-45deg);
  }

  &__left {
    // width: 100%;
  }

  &__oval {
    background: url("../images/main/ovalIn.svg");
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat;
    position: relative;
    height: 520px;
    width: 520px;
  }

  &__phone {
    // width: 100%;
    position: absolute;
    left: 53px;
    top: -5px;
    z-index: 1;

    &--second {
      transform: rotate(10deg);
    }
  }

  &__right {
    width: 100%;
    max-width: 518px;
    padding-top: 60px;
  }
}

@media screen and (max-width: 1200px) {
  .services {
    &__wrapper {
      margin: 0 30px;
    }
  }
}

@media screen and (max-width: 1024px) {
  .services {
    &__wrapper {
      margin: 0;
    }

    &__clasify {
      margin-bottom: 0;
    }

    &__block,
    &__clasify,
    &__claim {
      gap: 32px;
      grid-template-columns: 1fr;
      margin-top: 0;
      text-align: center;
    }

    &__right {
      max-width: 100%;
      // margin-bottom: 50px;
    }

    &__includes {
      flex-direction: column;
      display: flex;
      align-items: center;
    }

    &__phone--second,
    &__phone--third {
      display: none;
    }

    &__oval {
      // display: flex;
      // justify-content: center;
      position: relative;
      margin: 60px auto 0;
    }

    &__phone--first {
      // margin: 40px auto;
      // left: 255px;
      // top: 35px;
    }

    &__oval--second,
    &__oval--third {
      display: none;
    }

    &__heading {
      margin-top: 50px;
    }
  }
}

