.contact {
  &__wrapper {
    margin-left: 200px;
    margin-right: 200px;
    display: flex;
  }

  &__address {
    margin-top: 57px;
  }

  &__choose-city {
    display: flex;
    flex-direction: column;
  }

  &__heading {
    margin-bottom: 12px;
    max-width: 324px;
    width: 324px;
    font-size: 52px;
    font-weight: 700;
    line-height: 60px;
    letter-spacing: 0;
    color: $main-color;
  }

  &__text {
    max-width: 220px;
    width: 220px;
    font-size: 16px;
    font-weight: 400;
    line-height: 24px;
    letter-spacing: 0.2px;
    color: $text-color;
  }

  &__tap-map {
    margin-top: 5px;
    margin-left: 20px;
  }

  &__city-name {
    margin-bottom: 12px;
    color: $main-color;
    font-size: 18px;
    font-weight: 600;
    line-height: 28px;
    letter-spacing: 0.2px;
  }

  &__city-address {
    color: $text-color;
    width: 250px;
  }

  &__LA,
  &__NY,
  &__BN,
  &__DT {
    display: flex;
    flex-direction: row;
    gap: 19px;
    margin-top: 41px;
  }

  &__map {
    margin-bottom: 190px;
  }
}

@media screen and (max-width: 1200px) {
  .contact {
    &__wrapper {
      margin-left: 20px;
      margin-right: 20px;
    }
  }
}

@media screen and (max-width: 1024px) {
  .contact {
    &__wrapper {
      flex-direction: column;
      margin-left: 30px;
      margin-right: 30px;
    }

    &__choose-city {
      align-items: center;
    }

    &__heading {
      text-align: center;
    }

    &__map {
      padding-top: 20px;
      text-align: center;
    }
  }
}
